import React, { Component } from 'react';
import BottomMenu from '../BottomMenu/BottomMenu';
import classes from './PriceList.module.css';
import { render } from '@testing-library/react';

class PriceList extends Component {
    componentDidUpdate() {
        const scrollPosition = window.scrollY;
        if (scrollPosition !== 0) { 
            window.scrollTo(0, 0);
        }
    }
    
    
    render(){ 
        let elements = this.props.products.map((item, index) => {
            return (
                <div className={classes.PriceListItem} key={index}>
                    <div className={classes.ElementName}>{item.name}</div>
                    <div className={classes.ElementPrice}>{item.price.toFixed(2)}</div>
                </div> )
        });
        const category = this.props.location.pathname.split('/');
        const index = this.props.general.findIndex(el=> el[0] === category[1].toLowerCase());   
        return (
                <div>
                    <h2 className={classes.PriceListHeader}>{this.props.title}</h2>
                    <div id='price-list' className={classes.PriceListWrapper}>       
                        {elements}
                    </div>
                        {index === 0
                            ? <BottomMenu next={this.props.general[index+1]}/>
                            : index === this.props.general.length -1
                                ? <BottomMenu prev={this.props.general[index-1]} />
                                : <BottomMenu prev={this.props.general[index-1]} next={this.props.general[index+1]} />}
                        
                
                </div>
            );
        }
}

export default PriceList;
