import React from 'react';
import {NavLink} from 'react-router-dom';
import classes from './MainMenu.module.css';

const mainMenu = () => {
    return (
        <div className={classes.MainMenuWrapper}>
            <h4>ΤΙΜΟΚΑΤΑΛΟΓΟΣ</h4>
            <div className={classes.MainMenu}>
                <NavLink to='/food'>
                    <div className={classes.Item}><h3>ΦΑΓΗΤΑ</h3></div>
                </NavLink>

                <NavLink to='/drinks'>
                    <div className={classes.Item}><h3>ΠΟΤΑ</h3></div>
                </NavLink>             
            </div>
            <aside className={classes.Info}>
                <p>Όλα τα είδη είναι διαθέσιμα σε πακέτο μέσω της delivery service, μέχρι τις 23:00, με χρέωση ενός επιπλέον ευρώ στην παραγγελία</p>
            </aside>
        </div>
    )
}

export default mainMenu;
