import React from 'react';
import {Link} from 'react-router-dom'
import classes from './BottomMenu.module.css';

const bottomMenu = (props) => {
    return (
        <div className={classes.BottomMenuWrapper}>
            <div className={classes.BottomMenu}>
                {   props.prev 
                    ? <Link to={props.prev[0]}>
                        {props.prev[1]}</Link> 
                    : null}

                <Link 
                    to='/' 
                    className={classes.Central}
                    style={ !props.prev ? {gridColumn : '2/3'} : null} >
                        ΑΡΧΙΚΗ
                </Link> 

                {   props.next 
                    ? <Link to={props.next[0]}>
                        {props.next[1]}</Link> 
                    : null} 
            </div>
        </div>
    )
}

export default bottomMenu;
